.title{
font-family: 'Titan One';
font-weight: 400;
font-size: 34px;
line-height: 1.15;
text-align: center;
letter-spacing: 0.04em;
text-transform: uppercase;

    @media screen and (min-width: 768px) {
    font-size: 48px;
}
    @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;
}
}
