.container {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  outline: 1px solid rgb(224, 34, 145);

  @media screen and (min-width: 320px) {
    max-width: 320px ;
}
    @media screen and (min-width: 768px) {
    max-width: 768px ;
    padding-left: 32px;
    padding-right: 32px;
}
    @media screen and (min-width: 1280px) {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
}
}